.car-card {
  border: 1px solid #ddd;
  border-radius: 12px;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.car-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
}

.car-card__image {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.car-card__body {
  padding: 15px;
}

.car-card__title {
  margin: 0 0 8px 0;
  font-size: 1.2rem;
  color: #1f2937;
}

.car-card__info {
  color: #666;
  margin: 0 0 8px 0;
  font-size: 14px;
}

.car-card__price {
  font-size: 1.1rem;
  font-weight: bold;
  color: #2563eb;
  margin: 0 0 12px 0;
}
