.contact {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
}

.contact h1 {
  font-size: 2rem;
  margin-bottom: 10px;
  color: #1f2937;
}

.contact > p {
  color: #6b7280;
  margin-bottom: 40px;
}

.contact-form {
  background-color: #f8f9fa;
  padding: 30px;
  border-radius: 12px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
  box-sizing: border-box;
}

.contact-form textarea {
  min-height: 150px;
  resize: vertical;
}

.contact-form button {
  width: 100%;
  padding: 12px;
  background-color: #2563eb;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.contact-form button:hover {
  background-color: #1d4ed8;
}

.contact-info {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.contact-info__item {
  text-align: center;
  padding: 20px;
}

.contact-info__item h3 {
  margin: 10px 0 5px;
  color: #1f2937;
}

.contact-info__item p {
  color: #6b7280;
  font-size: 14px;
}
